.header-main, #header-sticky
  .primary-nav
    .main-menu-header
      li.menu-item
        @apply font-inria text-[15px] text-Brown font-normal leading-6 tracking-[0.03125rem] uppercase
        &:not(:last-child)
          @apply mr-11
        a
          position: relative
          display: inline-block
          &::before
            content: ""
            position: absolute
            left: 0
            bottom: 0.25em
            width: 100%
            height: 1px
            background-color: currentColor
            transform: scaleX(0)
            transform-origin: left center
            transition: transform 250ms ease-out
        &:hover > a::before,
        > a:focus-visible::before
          transform: scaleX(1)
        &.current-menu-item > a::before,
        &.current-menu-ancestor > a::before,
        &.current_page_item > a::before,
        &.current_page_ancestor > a::before
          transform: scaleX(1)

.header-main
  .primary-nav
    .main-menu-header
      @apply py-[37px]

#header-sticky
  .primary-nav
    .main-menu-header
      @apply justify-center

// Hamburger Menu
.menu-toggle
  &__button
    @apply flex items-center
  &__bars
    @apply flex flex-col items-start justify-center overflow-visible relative w-[28px] h-[19px]
  &__bar
    @apply w-full h-px rounded-md bg-Brown transition-all duration-500 ease-in-out translate-x-0 rotate-0 opacity-100 origin-center
    &:not(:last-child)
      @apply mb-[8px]
    &:first-child
      @apply w-[13px]
    &:nth-child(2)
      @apply w-5
    &:last-child
      @apply w-full

.menu-toggle__button.menu-open
  .menu-toggle__bars
    .menu-toggle__bar
      &:first-child
        @apply w-full rotate-45 translate-y-[9px]
      &:nth-child(2)
        @apply opacity-0 scale-x-0
      &:last-child
        @apply w-full -rotate-45 -translate-y-[9px]
        
// Sticky Header

/* when sticky is showing */
html.sticky-active #header-main
  --fade-dur: 400ms
  --fade-ease: ease-out
  opacity: 0
  pointer-events: none
  transition-delay: 100ms

/* default */
#header-main
  --fade-dur: 1200ms
  --fade-ease: cubic-bezier(.22,.61,.36,1)
  opacity: 1
  transition: opacity var(--fade-dur) var(--fade-ease)
  transition-delay: 300ms